
ul li {
  list-style: none;
}
.component-container {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
}
.options-container {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
}
.options-container .op-header {
  background: #eaeefb;
  width: 920px;
}
.options-container .op-header span {
  display: inline-block;
  width: 169px;
  padding-left: 10px;
  line-height: 50px;
  height: 50px;
}
.options-container .op-body {
  width: 920px;
  border-bottom: 1px solid #eaeefb;
  padding: 10px 0;
}
.options-container .op-body span {
  vertical-align: top;
  display: inline-block;
  width: 169px;
  padding-left: 10px;
  line-height: 32px;
}
.header {
  width: 100%;
  height: 80px;
  background: #5488F9;
}
.header .container {
  display: table;
  width: 1200px;
  margin: 0 auto;
  height: 80px;
}
.header .container .logo {
  display: table-cell;
  vertical-align: middle;
}
.header .container .logo-img {
  width: 150px;
}



